.sidebar {
  height: 100vh;
  grid-area: sidebar;
  background-color: #ffffff;
  box-shadow: 0px 4px 32px rgba(0, 0, 0, 0.05), 0px 8px 24px rgba(0, 0, 0, 0.1),
    0px 4px 8px rgba(0, 0, 0, 0.05);
}
.sidebar__wrapper {
  width: 266px;
  height: 100%;
  margin: 0 32px;
  padding-top: 52px;
}

.sidebar__top {
  display: flex;
  align-items: center;
  align-content: center;
  &-logo {
    width: 92px;
    height: 45px;
  }
  &-title {
    font-weight: normal;
    font-size: 14px;
    line-height: 1.14;
    margin-left: 19px;
  }
}

.profile__wrapper {
  display: flex;
  align-content: center;
  align-items: center;
  margin-top: 45px;
  margin-bottom: 72px;
}
.profile__avatar {
  width: 44px;
  height: 44px;
  border-radius: 50%;
}
.profile__name {
  font-weight: 500;
  font-size: 16px;
  line-height: 1;
  color: #2e2e2e;
  margin-left: 12px;
}
.profile__out-btn {
  display: flex;
  align-content: center;
  height: 20px;
  box-sizing: content-box;
  background: none;
  border: 0;
  cursor: pointer;
  color: inherit;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  margin-left: auto;
  -webkit-appearance: button;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.profile__out-btn {
  fill: var(--icon-color);
  transition: all 0.5s ease;
  &:hover {
    fill: var(--icon-color-hover);
    transform: scale(1.3);
  }
}

.sidebar__navigation {
  display: flex;
  flex-direction: column;
}
.navigation__icon {
  fill: var(--icon-color);
  transition: all 0.3s ease;
}

.navigation__icon svg > use#pharma {
  width: 35px;
}

.navigation__link {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  padding: 4px 0;
  transition: all 0.3s ease;
}

.navigation__link:hover .navigation__icon,
.navigation__link:hover .navigation__title {
  color: var(--icon-color-hover);
  fill: var(--icon-color-hover);
}

.navigation__title {
  margin-left: 28px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.4;
  color: #201f25;
  transition: all 0.3s ease;
}
