.modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  max-width: 800px;
  position: relative;
  margin: auto;
  padding: 0;
  background: #ffffff;
  box-shadow: 0px 4px 32px rgba(0, 0, 0, 0.05), 0px 8px 24px rgba(0, 0, 0, 0.1),
    0px 4px 8px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.4s;
}

@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

.modal-header {
  width: 100%;
  height: 50px;
  background-color: rgb(255, 255, 255);
  position: relative;
  top: 0;
  left: 0;
  border-radius: 8px;
}

.close {
  position: absolute;
  right: 21px;
  top: 21px;
  width: 32px;
  height: 32px;
  opacity: 0.5;
  cursor: pointer;
}
.close:hover {
  opacity: 1;
}
.close:before,
.close:after {
  position: absolute;
  left: 15px;
  content: " ";
  height: 33px;
  width: 3px;
  background-color: #1eb2e0;
}
.close:before {
  transform: rotate(45deg);
}
.close:after {
  transform: rotate(-45deg);
}

.modal-body {
  padding: 2px 16px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal-title {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.01em;
  color: #201f25;
}

// Modal select
.seldiv {
  max-width: 560px;
  width: 100%;

  margin-top: 46px;
  margin-bottom: 61px;
}
.__select {
  position: relative;
  width: 100%;
  height: 40px;
  margin: 0 auto;

  &[data-state="active"] {
    .__select__title {
      &::before {
        transform: translate(-3px, -50%) rotate(-45deg);
      }

      &::after {
        transform: translate(3px, -50%) rotate(45deg);
      }
    }

    .__select__content {
      opacity: 1;
    }

    .__select__label + .__select__input + .__select__label {
      max-height: 40px;
      border-top-width: 1px;
    }
  }
}
.__select__title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 8px 16px;

  border-radius: 5px;
  border: solid 1px #c7ccd1;

  cursor: pointer;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 50%;
    right: 16px;

    display: block;
    width: 10px;
    height: 2px;

    transition: all 0.3s ease-out;

    background-color: #333333;

    transform: translate(-3px, -50%) rotate(45deg);
  }
  &::after {
    transform: translate(3px, -50%) rotate(-45deg);
  }

  &:hover {
    border-color: hsl(194, 76%, 50%);

    &::before,
    &::after {
      background-color: hsl(194, 76%, 50%);
    }
  }
}

.__select__content {
  position: absolute;
  top: 40px;
  left: 3px;

  display: flex;
  flex-direction: column;
  width: calc(100% - 6px);

  background-color: #ffffff;

  border: 1px solid #c7ccd1;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;

  transition: all 0.3s ease-out;

  opacity: 0;
  z-index: 8;
}
.__select__input {
  display: none;

  &:checked + label {
    background-color: #dedede;
  }
  &:disabled + label {
    opacity: 0.6;
    pointer-events: none;
  }
}
.__select__label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  max-height: 0;
  padding: 0 16px;

  transition: all 0.2s ease-out;

  cursor: pointer;

  overflow: hidden;

  & + input + & {
    border-top: 0 solid #c7ccd160;
  }

  &:hover {
    background-color: hsl(194, 76%, 50%) !important;

    color: #ffffff;
  }
}

.select-title {
  display: flex;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #515c73;
  margin-bottom: 5px;
}

// Textarea

.textarea__wrapper {
  text-align: center;
  margin: 24px auto 52px;
  max-width: 560px;
  width: 100%;
}

textarea {
  color: #444;
  padding: 14px;
  width: 100%;
  border-radius: 5px;
  border: solid 1px #c7ccd1;
}
.txta {
  width: 100%;
  max-width: 560px;
  min-height: 100px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  overflow: hidden;
  line-height: 1.4;
  &::placeholder {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #acb2bd;
  }
}

.form-btn {
  display: flex;
  text-align: center;
  justify-content: space-evenly;
}

.btn_puch,
.reset {
  display: flex;
  border: solid 2px #1eb2e0;
  background: #1eb2e0;
  border-radius: 8px;
  transition: all 0.3s ease-out;
  padding: 18px 24px;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  cursor: pointer;
}

.btn_puch {
  background-color: hsl(194, 76%, 50%);
  color: hsl(0, 0%, 100%);
  &:hover {
    background-color: hsl(0, 0%, 100%);
    color: hsl(194, 76%, 50%);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1), 0px 0px 4px rgba(0, 0, 0, 0.15);
  }
  &:active {
    background: hsl(191, 92%, 42%);
    color: rgba(216, 248, 255, 0.8);
  }
}

.reset {
  background-color: hsl(0, 0%, 100%);
  color: hsl(194, 76%, 50%);
  &:hover {
    background-color: hsl(194, 76%, 50%);
    color: hsl(0, 0%, 100%);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1), 0px 0px 4px rgba(0, 0, 0, 0.15);
  }
  &:active {
    background: hsl(191, 92%, 42%);
  }
}

textarea {
  display: block;
  width: 100%;
  background: white no-repeat;
  background-image: linear-gradient(
      to bottom,
      hsl(194, 76%, 50%),
      hsl(194, 76%, 50%)
    ),
    linear-gradient(to bottom, silver, silver);
  background-size: 0 2px, 100% 1px;
  background-position: 50% 100%, 50% 100%;
  transition: background-size 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
}
textarea:focus {
  background-size: 100% 2px, 100% 1px;
  outline: none;
}
