/*Global styles*/
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  font-family: "Inter", sans-serif;
}

section {
  padding: 3.9rem 0;
  overflow: hidden;
}

img {
  width: 100%;
  max-width: 100%;
}

a {
  text-decoration: none;
}

.wrapper {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  height: 100vh;
  display: grid;
  grid-template-columns: 330px 1fr;
  grid-template-rows: auto;
  grid-template-areas: "sidebar content";
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
  margin: 0;
  padding: 0;
}

