.content {
  height: 100vh;
  grid-area: content;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 180px 1fr;
  grid-template-areas:
    "header"
    "section";
  &__top {
    grid-area: header;
    background: linear-gradient(180deg, #eef2f5 0%, #f4f5fa 100%);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    padding: 0 44px;
  }
  &__wrapper {
    grid-area: section;
    padding: 24px 44px;
  }
}

.header__title {
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.01em;
  color: #201f25;
  padding-bottom: 40px;
}
.header__subtitle {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 24px;
  letter-spacing: 0.01em;
  color: #1eb2e0;
  padding-bottom: 15px;
  border-bottom: 4px solid #1eb2e0;
  animation: move 3s linear infinite;
}
@keyframes move {
  0% {
    opacity: 0.25;
  }
  25% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0.5;
  }
  100% {
    opacity: 0.25;
  }
}

.btn-modal {
  background: #1eb2e0;
  border-radius: 8px;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
  box-sizing: content-box;
  border: 0;
  cursor: pointer;
  overflow: visible;
  padding: 12px 16px;
  transition: all 0.3s ease;
  &:hover {
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1), 0px 0px 4px rgba(0, 0, 0, 0.15);
  }
  &:active {
    background: #09abcf;
    color: rgba(216, 248, 255, 0.8);
  }
}

// Table

@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);

/*** basic styles ***/

.table-client {
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: left;
  margin-top: 42px;
}

label {
  font-size: 24px;
  padding: 12px;
}

.table-head {
  display: grid;
  min-width: 100%;
  grid-template-columns:
    minmax(30px, 50px)
    minmax(50px, 100px)
    minmax(50px, 1.1fr)
    minmax(50px, 200px)
    minmax(50px, 180px)
    minmax(50px, 1.6fr);
  span {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    overflow: hidden;
    width: 100%;
    white-space: nowrap;
    text-align: left;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    color: #acb2bd;
  }
}
.table-body {
  display: grid;
  min-width: 100%;
  grid-template-columns:
    minmax(20px, 35px)
    minmax(50px, 100px)
    minmax(50px, 1.1fr)
    minmax(50px, 200px)
    minmax(50px, 180px)
    minmax(50px, 1.6fr);
  span {
    overflow: hidden;
    width: 100%;
    white-space: nowrap;
    text-align: left;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #201f25;
  }
  &:hover {
    background: #eef2f5;
    border-radius: 8px;
  }
}

/*** custom checkboxes ***/

input[type="checkbox"] {
  display: none;
} /* to hide the checkbox itself */
input[type="checkbox"] + label:before {
  font-family: FontAwesome;
  display: inline-block;
}

input[type="checkbox"] + .table-head:before {
  content: "\f096";
  color: #d2d6de;
} /* unchecked icon */
input[type="checkbox"] + .table-head:before {
  letter-spacing: 10px;
} /* space between checkbox and label */

input[type="checkbox"]:checked + .table-head:before {
  content: "\f146";
  color: #acb2bd;
} /* checked icon */
input[type="checkbox"]:checked + .table-head:before {
  letter-spacing: 6.6px;
} /* allow space for check mark */

input[type="checkbox"] + .table-body:before {
  content: "\f096";
  color: #d2d6de;
} /* unchecked icon */
input[type="checkbox"] + .table-body:before {
  letter-spacing: 10px;
} /* space between checkbox and label */

input[type="checkbox"]:checked + .table-body:before {
  content: "\f14a";
  color: #30be61;
} /* checked icon */
input[type="checkbox"]:checked + .table-body:before {
  letter-spacing: 6.6px;
} /* allow space for check mark */

input[type="checkbox"]:checked + .table-body > span {
  color: #30be61;
}

.table-body span svg {
  margin-right: 12px;
}

@media only screen and (max-width: 1440px) {
  .table-client .farm,
  .farm {
    display: none;
  }

  .table-head {
    display: grid;
    grid-template-columns:
      minmax(30px, 50px)
      minmax(50px, 100px)
      minmax(50px, 1.2fr)
      minmax(50px, 160px)
      minmax(50px, 1.6fr);
  }

  .table-body {
    display: grid;
    min-width: 100%;
    grid-template-columns:
      minmax(20px, 35px)
      minmax(50px, 100px)
      minmax(50px, 1.2fr)
      minmax(50px, 160px)
      minmax(50px, 1.6fr);
  }
}
